<template>
    <div>
        <tables
        :title = "'User Api Keys'"
        :headers = "TableHeaders"
        :users = "items"
        :currentEditedItem = "editedItem"
        :defaultItem = "defaultItem"
        :dialogBoxEditFields = "dialogBoxEditFields"
    ></tables>
    <button v-on:click="getList">get Use</button>
    </div>
</template>

<script>
import tables from './ui/Tables';
import { Items as ApiKeys } from '@/api/apiKeys';
import axios from 'axios'; 

export default {
  name: 'userApiKeys',
  components: {
    tables
  },
  data () {
    return {
      search: '', 
      TableHeaders: [
        {
          text: 'User',
          value: 'user'
        },                    
        {
          text: 'Key',
          value: 'key'
        },
        {
          text: 'Source',
          value: 'source'
        },
        {
          text: 'Action',
          value: 'action'  
        }
      ],
      items: ApiKeys,
      editedItem: {
        user: '',
        key: '',
        source: ''
      },
      defaultItem: {
        user: '',
        key: '',
        source: ''
      },
      currentUser: [
        'rss',
        'riddells',
        'wmts'
      ],
    };
  },
  computed: {
    dialogBoxEditFields () {
      return [
        {
          value: 'key',
          type: 'text',
          label: 'Key'
        },
        {
          value: 'user',
          type: 'dropdown',
          label: 'User',
          items: this.currentUser
        }
      ];
    }
  },
  methods: {
    getList(event) {
      axios.get('/users')
      .then(function(response) {
        console.log(response.data);
      });
    }
  }        
};
</script>


