<template>
    <div>
        <tables
            :title = "'Users'"
            :headers = "TableHeaders"
            :users = "users"
            :currentEditedItem = "editedItem"
            :defaultItem = "defaultItem"
            :dialogBoxEditFields = "dialogBoxEditFields"
        ></tables>
    </div>
</template>
<script>
import tables from './ui/Tables';
import { Items as Users } from '@/api/user';
export default {
  name: 'users',
  components: {
    tables
  },
  data () {
    return {
      TableHeaders: [
        {
          text: 'User Name',
          value: 'userName'
        },                    
        {
          text: 'Email',
          value: 'email'
        },
        {
          text: 'Last login',
          value: 'lastLogin'
        },
        {
          text: 'Registered devices',
          value: 'registeredDevices'
        },
        {
          text: 'Allowed devices',
          value: 'allowedDevices'
        },
        {
          text: 'Configuration',
          value: 'configuration'
        },
        {
          text: 'Action',
          value: 'action'  
        }
      ],
      users: Users,
      editedItem: {
        userName: '',
        email: '',
        lastLogin: '',
        registeredDevices: '',
        allowedDevices: '',
        configuration: '',
        Admin: ''
      },
      defaultItem: {
        userName: '',
        email: '',
        lastLogin: '',
        registeredDevices: '',
        allowedDevices: '',
        configuration: '',
        Admin: ''
      },      
      configurations: [
        'rss',
        'riddells',
        'ecw'
      ]     
    };
  },
  computed: {
    dialogBoxEditFields () {
      return [
        {
          value: 'email',
          type: 'text',
          label: 'Email'
        },
        {
          value: 'allowedDevices',
          type: 'number',
          label: 'Allowed devices'          
        },
        {
          value: 'Admin',
          type: 'checkbox',
          label: 'Is admin?'   
        },
        {
          value: 'configuration',
          type: 'dropdown',
          label: 'Configuration',
          items: this.configurations
        }
      ];
    }     
  }
};
</script>


