const Menu =  [
  {
    title: 'Dashboard',
    group: 'apps',
    icon: 'dashboard',
    name: 'Dashboard',
  },
  {
    title: 'Users',
    group: 'apps',
    name: 'Users',
    icon: 'people',
  },
  {
    title: 'Layers',
    group: 'apps',
    name: 'Layers',
    icon: 'layers',
  },
  {
    title: 'Configurations',
    group: 'apps',
    name: 'Configurations',
    icon: 'settings'
  },
  {
    title: 'Redlines',
    group: 'apps',
    name: 'Redlines',
    icon: 'create',
  },  
  
  {
    title: 'Blackline Specifications',
    group: 'apps',
    name: 'Blackline Specifications',
    icon: 'people',
  },
  {
    title: 'Feature Links',
    group: 'apps',
    name: 'FeatureLinks',
    icon: 'link',
  },
  {
    title: 'Forms',
    group: 'apps',
    name: 'Forms',
    icon: 'people',
  },
  {
    title: 'User Api Keys',
    group: 'apps',
    name: 'UserApiKeys',
    icon: 'vpn_key',
  },
  {
    title: 'Licenses',
    group: 'apps',
    name: 'Licenses',
    icon: 'people',
  },
  {
    title: 'Preview',
    group: 'apps',
    name: 'Preview',
    icon: 'people',
  }
  
  // {
  //   title: 'Pages',
  //   group: 'extra',
  //   icon: 'list',
  //   items: [
  //     { name: 'Login', title: 'Login', component: 'Login' },
  //     { name: '404', title: '404', component: 'NotFound' },
  //     { name: '403', title: '403', component: 'AccessDenied' },
  //     { name: '500', title: '500', component: 'ServerError' },
  //   ]
  // },
];
// reorder menu
Menu.forEach((item) => {
  if (item.items) {
    item.items.sort((x, y) => {
      let textA = x.title.toUpperCase();
      let textB = y.title.toUpperCase();
      return (textA < textB) ? -1 : (textA > textB) ? 1 : 0;
    });
  }
});

export default Menu;



