<template>
  <div id="pageTable">
    <v-container grid-list-xl fluid>
      <v-layout row wrap>
        <v-flex sm12>
          <h3>{{tableTitle}}</h3>
        </v-flex>        
        <v-flex lg12>
          <v-card>
            <v-toolbar card color="white">
              <v-text-field
              flat
              solo
              prepend-icon="search"
              placeholder="Type something"
              v-model="search"
              hide-details
              class="hidden-sm-and-down"
              ></v-text-field>     
              <v-btn icon>
                <v-icon>filter_list</v-icon>
              </v-btn>         
            </v-toolbar>
            <v-divider></v-divider>

            <v-dialog v-model="dialog" fullscreen hide-overlay transition="dialog-bottom-transition">
                <v-btn slot="activator" color="primary" dark class="mb-2">Add New</v-btn>
                <v-card>
                  <v-toolbar dark color="primary">
                    <v-btn icon dark @click.native="dialog = false">
                      <v-icon>close</v-icon>
                    </v-btn>
                    <v-toolbar-title>{{ formTitle }}</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-toolbar-items>
                      <v-btn dark flat @click.native="save">Save</v-btn>
                    </v-toolbar-items>
                  </v-toolbar>
                  <v-card-text>
                    <v-container grid-list-md>
                      <v-layout wrap>
                        <v-flex xs12 ref="form">
                          <div v-for="field in dialogBoxEditFields" :key="field.label"> 
                            <v-text-field v-if="field.type == 'text'" v-model="editedItem[field.value]" label="Email" clearable></v-text-field>                           
                            <v-text-field v-else-if="field.type == 'number'" type="number" :min="0" v-model="editedItem[field.value]" label="Allowed devices"></v-text-field>    
                            <v-checkbox v-else-if="field.type == 'checkbox'" v-model="editedItem[field.value]" :label="`Is admin?`"></v-checkbox>              
                            <v-autocomplete v-else-if="field.type == 'dropdown'" :items="field['items']" v-model="editedItem[field.value]" label="Configuration" placeholder="Select..." required ></v-autocomplete>
                          </div>
                        </v-flex>
                      </v-layout>
                    </v-container>
                  </v-card-text>        
                </v-card>
              </v-dialog>
            
            <v-card-text class="pa-0">
              <v-data-table
                :headers="complex.headers"
                :search="search"
                :items="complex.items"
                :rows-per-page-items="[10,25,50,{text:'All','value':-1}]"
                class="elevation-1"
                item-key="name"
                v-model="complex.selected"
                >
                <template slot="items" slot-scope="props">
                  <td v-for="header in complex.headers" v-if="header.value != 'action'" :key="header.value">
                    <span v-if="header.value =='userName' && props.item.Admin">{{ props.item.userName }}( {{props.item.Admin? 'admin' : 'regular'}} )</span>
                    <span v-else>{{props.item[header.value]}}</span>
                  </td>
                  <td>
                    <v-btn @click="editItem(props.item)" depressed outline icon fab dark color="primary" small>
                      <v-icon>edit</v-icon>
                    </v-btn>
                    <v-btn @click="deleteItem(props.item)" depressed outline icon fab dark color="pink" small>
                      <v-icon>delete</v-icon>
                    </v-btn>
                  </td>
                </template>
              </v-data-table>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
export default {
  name: 'tables',
  props: {
    title: {
      type: String,
      required: true
    },
    headers: {
      type: Array,
      required: true
    },
    users: {
      type: Array,
      required: true
    },
    currentEditedItem: {
      type: Object,
      required: true
    },
    defaultItem: {
      type: Object,
      required: true
    },
    dialogBoxEditFields: {
      type: Array,
      required: true
    }
  },
  data () {
    return {
      dialog: false,
      search: '',
      editedIndex: -1,
      editedItem: this.currentEditedItem,      
      complex: {
        selected: [],        
        items: this.users,
        headers: this.headers
      },
    };
  },
  computed: {
    tableTitle () {
      return this.title;
    },
    formTitle () {
      return this.editedIndex === -1 ? 'New User' : 'Edit User';
    },
    form () {
      return {
        email: this.editedItem.email,
        allowedDevices: this.editedItem.allowedDevices,
        configuration: this.editedItem.configuration
      };
    }
  },
  watch: {
    dialog (val) {
      val || this.close();
    }
  },
  methods: {
    editItem (item) {
      this.editedIndex = this.complex.items.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    deleteItem (item) {
      this.editedIndex = this.complex.items.indexOf(item);
      this.complex.items.splice(this.editedIndex, 1);
    },
    close () {
      this.dialog = false;
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      }, 300);
    },
    save () {
      if (this.editedIndex > -1) {
        Object.assign(this.complex.items[this.editedIndex], this.editedItem);
      } else {
        if (this.editedItem.allowedDevices === '') {
          this.editedItem.allowedDevices = 0;
        }
        this.complex.items.push(this.editedItem);
      }
      this.close();
    }
  }
};
</script>
<style>
  #tableData{
    border: 0;
    outline: 0;
    background: transparent;
    color: black;
    border-bottom: 1px solid grey; 
  }
</style>


